*, *::before, *::after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, ol, li, p {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

/* Heder */
.header {
  padding: 20px 40px;
  background-color: #4D8FC3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.header__title {
  color: #EAEEEF;
}

.header__login {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #4D8FC3;
  transition: all 0.1s linear;
}

.header__login:hover {
  background-color: #A7C4D4;
  border-color: #A7C4D4;
}

/* Auth */
.auth {
  padding: 20px 40px;
  text-align: center;
}

.auth__title {
  margin-bottom: 20px;
  font-size: 2rem;
}

/* Form */
.form {
  position: relative;
  padding: 20px;
  margin: 0 auto 25px;
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: #f4f7fc;
  box-shadow: 0 0 8px 1px #4D8FC3;
  animation: smoothShow 0.5s linear;
}

.form--addnote {
  max-width: 800px;
  display: flex;
  flex-direction: row;
}

.form__title {
  text-align-last: left;
  margin-bottom: 25px;
  font-size: 1.5rem;
  font-weight: 400;
}

.form--addnote textarea {
  width: calc(100% - 130px);
  padding: 9px;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  border: none;
  outline: none;
  background-color: transparent;
  border: 2px solid #A7C4D4;
  border-right: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  resize: none;
}

.form__input {
  padding: 5px 10px 5px 0;
  margin-bottom: 10px;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  border: none;
  outline: none;
  background-color: transparent;
  border-bottom: 2px solid #A7C4D4;
}

.form__btn {
  margin-top: 15px;
  margin-bottom: 25px;
  padding: 10px;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  letter-spacing: 1px;
  background-color: #4D8FC3;
  color: #fff;
  border: 2px solid #4D8FC3;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.1s linear;
}

.form__btn:hover {
  background-color: #EAEEEF;
  color: #4D8FC3;
}

.form__btn--addnote {
  width: 130px;
  margin: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.form__btn--addnote:hover {
  border: 2px solid #A7C4D4;

}

.close__btn {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 1;
  border: none;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  background-color: transparent;
  color: #FD292F;
  transition: all 0.1s linear;
}

.close__btn:hover {
  background-color: #FD292F;
  color: #f4f7fc;
}
 
.form__text {
  margin-bottom: 10px;
}

.form span {
  font-weight: 500;
  color: #4D8FC3;
  cursor: pointer;
}

.form span:hover {
  text-decoration: underline;
}

.form__list {
  list-style: none;
}

@keyframes smoothShow {
  0% {
    opacity: 0;
    top: -20px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}

/* UserMenu */
.usermenu {
  list-style: none;
  position: absolute;
  top: 65px;
  right: 20px;
  z-index: 1;
  background-color: #f4f7fc;
  border-radius: 10px;
  border: 2px solid #A7C4D4;
  overflow: hidden;
}

.usermenu__item {
  padding: 10px;
}

.usermenu__link {
  display: block;
  width: 100%;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: #000;
}

.usermenu__item:hover {
  background-color: #d3d7d8;
}

/* Main */
.main {
  padding: 20px;
}

/* Notes */
.notes {
  width: 100%;
  max-width: 1090px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.notes__message {
  font-size: 1.5rem;
}

/* Note */
.note {
  width: 100%;
  max-width: 350px;
  height: 175px;
  padding: 15px;
  border: 2px solid #A7C4D4;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
}

.note__triangle {
  width: 0;
  height: 0;
  border-top: 15px solid #058240;
  border-right: 15px solid #058240;
  border-bottom: 15px solid transparent;
  border-left: 15px solid transparent;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.note__checkblock {
  display: flex;
  align-items: center;
}

.note__checkblock input {
  margin-bottom: 4px;
  width: 15px;
  height: 15px;
}

.note__text {
  font-size: 1rem;
  font-weight: 700;
}

.note__date {
  font-size: 0.9rem;
  color: #686461;
}

.note__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.note__btn {
  margin-left: 5px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: all 0.1s linear;
}

.note__btn:hover {
  opacity: 0.6;
}

/* Modal */
.modal {
  padding: 20px;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(0);
}

.modal.active {
  transform: scale(1);
}

.modal__content {
  padding: 20px 20px 10px;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  position: relative;
}

.modal__name {
  font-size: 1.6rem;
  margin-bottom: 10px;
}

.modal__text {
  font-size: 1.2rem;
  margin-bottom: 10px;
  font-weight: 700;
}

.modal__text span {
  font-weight: 400;
}

/* Media */
@media(max-width: 768px) {
  .note {
    max-width: 100%;
  }
}

@media(max-width: 500px) {
  .form--addnote {
    flex-direction: column;
  }

  .form--addnote textarea {
    width: 100%;
    border-right: 2px solid #A7C4D4;
    border-radius: 5px;
  }

  .form__btn--addnote {
    margin-top: 10px;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
}